* {
    padding: 0;
    margin: 0;
    list-style: none;
    border: none;
    text-decoration: none;
    box-sizing: border-box;
    -webkit-overflow-scrolling: touch;
    font-family: "Mulish", sans-serif;

    &:focus {
        outline: 0;
    }
}

body {
    background: #f7f8fc;
    display: grid;
    place-items: center;
    height: 100vh;
}

.component {
    &--trial-box {
        background: #fff;
        box-shadow: 0 28px 30px -12px #e9ecf9;
        padding: 55px 45px;
        border-radius: 5px;
        width: 566px;

        h3 {
            font-size: 22px;
            font-weight: bold;
            color: #10182f;
            margin-bottom: 25px;
        }
        .credit {
            display: flex;
            align-items: center;
            margin-bottom: 70px;

            .circle-bar {
                width: 72px;
                height: 72px;
                transform: rotate(-90deg);
                margin-right: 20px;
                svg {
                    // --circum: calc(2 * 3.14 * 33.5);
                    // --offset: calc(var(--circum)- (var(--circum)*60/100));
                    circle {
                        fill: none;
                        stroke-width: 5px;
                        stroke: #dae1f5;
                        stroke-dasharray: var(--circum);
                        stroke-dashoffset: var(--circum);
                        stroke-linecap: round;
                        &:nth-child(1) {
                            stroke-dashoffset: 0;
                        }
                        &:nth-child(2) {
                            stroke: #345ace;
                            animation: 3s animateOffset;
                            animation-fill-mode: forwards;
                        }
                    }
                }
            }
            .amount{
                font-size: 22px;
                font-weight: bold;
                color: #10182f;
            }
            .text{
                font-size: 16px;
                color: #545d7a;
            }
        }
        p {
            font-size: 16px;
            line-height: 22px;
            color: #10182f;
            margin-bottom: 40px;
        }
        .action {
            display: flex;
            a {
                margin-right: 15px;
                height: 43px;
                border: 2px solid #dae1f5;
                border-radius: 43px;
                display: flex;
                align-items: center;
                padding: 0 35px;
                font-size: 16px;
                font-weight: bold;
                color: #10182f;
                transition: 300ms all;
                &:hover {
                    color: #345ace;
                    border-color: #345ace;
                }
                &.active {
                    background: #10182f;
                    color: #dae1f5;
                    border-color: #10182f;
                    &:hover{
                        background: #345ace;
                        border-color: #345ace;
                    }
                }
            }
        }
    }
}

@keyframes animateOffset {
    to {
        stroke-dashoffset: var(--offset);
    }
}
